.complete-container{
    height: 92vh;
    width: 100%;
}

.complete-msg{
    margin-left: auto;
    margin-right: auto;
    height: 30vh;
    width: 50%;
    border: 2px solid rgb(180,180,180,0.35);
    border-radius: 2vh;
    text-align: center;
    transform: translate(0,75%);
}

.loading-container{
    margin-left: auto;
    margin-right: auto;
    height: 50vh;
    width: 50%;
    transform: translate(0,50%);
}

.complete-txt{
    font-size: 2vh;
    margin-top: 5%;
}

